
.user-page{
    padding: 10px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.user-btn{
    background-color: rgb(133, 184, 231);
    font-weight: 400;
}

.grid-container {
    display: grid;
    gap: 16px;
    padding: 16px;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    width: 100%; /* Ensure the grid takes full width */
    box-sizing: border-box; /* Include padding in width calculation */
  }
  

  .popup-overlay {
    display: none;
  
    position: fixed;
  
    top: 0;
  
    left: 0;
  
    width: 100%;
  
    height: 100%;
  
    background: rgba(0, 0, 0, 0.5);
  }
  
  .popup {
    min-width: 220px;
  
    font-family: Arial, sans-serif;
  
    position: absolute;
  
    top: 50%;
  
    left: 50%;
  
    transform: translate(-50%, -50%);
  
    background: rgb(201, 227, 231);
  
    padding: 20px;
  
    border-radius: 8px;
  
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  .popup-content {
    text-align: center;
  }
  
  .popup .close {
    position: absolute;
  
    top: 10px;
  
    right: 10px;
  
    font-size: 20px;
  
    cursor: pointer;
  
    color: #333;
  }
  
  .popup button {
    background-color: #4caf50;
  
    color: #fff;
  
    padding: 10px 20px;
  
    border: none;
  
    border-radius: 4px;
  
    cursor: pointer;
  
    font-size: 16px;
  }
  
.text-pop-input {
    width: 80%;
  
    padding: 10px;
  
    margin: 10px 0;
  
    border: 1px solid #ddd;
  
    border-radius: 4px;
  }